@import '~styles/index';

.header-wrapper {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  width: 100%;
  height: 80px;
  background-color: $primary-grey-semi-light;
  box-shadow: 0 3px 5px rgba($primary-grey-dark, 0.5);
  transition: all 0.3s ease;
  z-index: 100;
  display: flex;
  justify-content: space-between;
  padding: 0 40px;
  align-items: center;

  @include mobile {
    padding: 0 20px;
  }

  &.small {
    height: 60px;

    .title {
      font-size: 2em;

      @include mobile {
        font-size: 1.2em;
      }
    }

    .github-icon-wrapper {
      width: 25px;
      height: 25px;
    }
  }

  .title {
    color: $primary-grey-dark;
    font-weight: bold;
    font-size: 3em;
    text-transform: uppercase;
    text-align: center;
    transition: all 0.3s ease;

    @include mobile {
      font-size: 1.5em;
    }
  }

  .github-icon-wrapper {
    display: block;
    width: 40px;
    height: 40px;
    transition: all 0.3s ease;

    @include mobile {
      width: 30px;
      height: 30px;
    }

    .github-icon {
      background-image: url('~icons/github-icon.svg');
      background-position: center;
      background-repeat: no-repeat;
      width: 100%;
      height: 100%;
      background-size: contain;
    }
  }
}
