@import '~styles/index';

.base-layout-wrapper {
  width: 100%;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: $primary-grey-light;

  @include desktop {
    padding: 0 0 20px 0;
  }

  @include mobile-tablet {
    padding: 0px 15px 20px 15px;
  }

  .content-wrapper {
    width: 100%;
    box-sizing: border-box;
    background-color: $primary-grey-semi-light;
    box-shadow: 0 10px 20px rgba($primary-grey-dark, 0.2), 0 6px 6px rgba($primary-grey-dark, 0.3);
    padding: 0 20px;
    margin-top: 120px;

    @include tablet {
      padding: 0 40px;
    }

    @include desktop {
      max-width: 768px;
      padding: 0 40px;
    }
  }
}
