@import '~styles/index';

.custom-button {
  height: 60px;
  min-width: 200px;
  font-size: 1.2em;
  padding: 0 20px;
  border: 0;
  cursor: pointer;
  text-transform: uppercase;
  background-color: rgba($primary-grey-medium, 0.5);
  color: $primary-grey-semi-light;
  box-shadow: 0px 0px 5px 1px rgba($primary-grey-dark, 0.4);
  transition: all 0.2s ease;

  &:hover {
    background-color: $primary-grey-medium;
    box-shadow: 0px 0px 5px 1px rgba($primary-grey-dark, 0.6);
  }
}

.button-wrapper {
  width: 100%;
  margin: 20px 0px;
  display: flex;

  &.left {
    justify-content: flex-start;
  }
  &.center {
    justify-content: center;
  }
  &.right {
    justify-content: flex-end;
  }
}
