@import '~styles/index';

.example-hyper-modal-wrapper {
  .content {
    padding-bottom: 40px;
  }

  .hyper-modal-wrapper {
    z-index: 1000;
    .hyper-modal-content-wrapper {
      display: flex;
      justify-content: center;
      align-items: center;
      @include mobile {
        width: 90%;
      }
    }

    .modal-content {
      .hyper-modal-default-content {
        padding: 30px 0;
        .title {
          color: #282c34;
        }
        .description {
          text-align: center;
        }
      }
    }
  }

  .modal-fullscreen {
    width: 100% !important;
    height: 100%;
    border-radius: 0px;
    .description {
      text-align: center;
    }
  }
}
